import React from 'react';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import { useParams } from 'react-router-dom';
import { CardContent } from '@material-ui/core';
import Anchor from 'modules/shared/components/Anchor';
import Loading from 'modules/shared/components/Loading';
import { GET_PURCHASE_BY_ID } from 'modules/shared/utils/consts';
import { useQuery } from 'react-query';
import useGetCategories from 'modules/categories/hooks/useGetCategories';
import GalleryHeader from 'modules/shared/layout/GalleryHeader';
import ErrorInfo from 'modules/shared/components/ErrorInfo';
import { getPurchaseByPackageId } from '../services/images';

const Gallery = () => {
  const params = useParams();
  const {
    categoriesById,
    isLoading: categoriesLoading,
    error: categoriesError,
  } = useGetCategories();

  const {
    isLoading: postsLoading,
    error: postsError,
    data: postsResult,
  } = useQuery(GET_PURCHASE_BY_ID, () =>
    getPurchaseByPackageId({
      purchaseId: params.purchaseId,
    }),
  );

  if (!params.purchaseId) {
    return (
      <Container className="d-flex align-items-center h-500">
        <Typography variant="body1">
          Voc deve fornecer uma compra vlida.
        </Typography>
      </Container>
    );
  }

  if (categoriesLoading || postsLoading) {
    return <Loading />;
  }

  if (categoriesError || postsError) {
    return <ErrorInfo errors={[categoriesError, postsError]} />;
  }

  const { postsByCategory } = postsResult.data;
  return (
    <>
      <GalleryHeader />
      <Container>
        {Object.keys(postsByCategory).map((category) => {
          const images = postsByCategory[category].posts;
          return (
            <Card key={category} className="h-100 mt-4 w-100">
              <CardContent>
                <Typography variant="h5" className="mb-4">
                  {categoriesById[category].name}
                </Typography>
                <Grid className="mt-1" container>
                  {images.map(({ id, aws_path: awsPath }) => {
                    const img = `${awsPath}`;
                    return (
                      <Grid key={id} item xs={12} sm={6} md={4} lg={2}>
                        <div className="w-100 d-flex flex-column align-items-center">
                          <Anchor url={img}>
                            <img
                              src={img}
                              style={{
                                width: '100%',
                                minHeight: '100%',
                                borderRadius: '2px',
                              }}
                              alt="Post"
                            />
                          </Anchor>
                        </div>
                      </Grid>
                    );
                  })}
                </Grid>
              </CardContent>
            </Card>
          );
        })}
      </Container>
    </>
  );
};

export default Gallery;
