import React from 'react';
import './package.scss';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';

export default function PackageCard({ name, description, isPublished, onEdit, onRemove, onPublish}) {
  return (
    <Card className="h-100 package-card">
      <CardContent className="h-100 d-flex flex-column justify-content-between">
        <div>
          <Typography variant="h6">{name}</Typography>
          <Typography className="mt-2" variant="body1">
            {description}
          </Typography>
        </div>
        <div>
          <Button
            onClick={onEdit}
            className="mt-4 mr-2"
            variant="contained"
            color="secondary"
          >
            Editar
          </Button>
          <Button
            onClick={onRemove}
            className="mt-4 red-button"
            variant="contained"
          >
            Remover
          </Button>
          {!Boolean(isPublished) && (
            <Button
              onClick={onPublish}
              className="mt-4 ml-2"
              color="primary"
              variant="contained"
            >
              Publicar
            </Button>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
