import watermark from 'watermarkjs';

const x = function (xCordinate) {
  return function (boat, metrics, context) {
    return xCordinate;
  };
};

const y = function (yCordinate) {
  return function (boat, metrics, context) {
    return yCordinate;
  };
};
/**
 * calculates the initial position of any given string
 * @param {String} givenString the string to insert into watermark
 * @param {*} refferenceValue the center of the watermark
 */
function calculatePosition(givenString, refferenceValue) {
  const pixelMultiplier = 1;
  return Math.ceil(refferenceValue - givenString.length * pixelMultiplier);
}

const roleMap = {
  0: 'Consultora',
  1: 'Diretora',
};

async function watermarkGen(baseImg, args) {
  const firstName = args.name.toUpperCase();
  const lastName = args.lastName.toUpperCase();
  const role = roleMap[args.role || 1];

  const contact = args.contact === '0' ? args.instagram : args.phone;
  const img = await watermark([baseImg])
    .image(
      watermark.text.atPos(
        x(calculatePosition(firstName, 90)),
        y(85),
        `${firstName}`,
        '16px Lato Italic',
        '#000',
        1,
      ),
    )
    .render()
    .image(
      watermark.text.atPos(
        x(calculatePosition(lastName, 92)),
        y(105),
        `${lastName}`,
        '16px Lato Italic',
        '#000',
        1,
      ),
    )
    .render()
    .image(
      watermark.text.atPos(x(65), y(50), 'MARY KAY', '10px Lato', '#000', 1),
    )
    .render()
    .image(
      watermark.text.atPos(x(70), y(35), `${role}`, '10px Lato', '#000', 1),
    )
    .render()
    .image(
      watermark.text.atPos(
        x(calculatePosition(contact, 70)),
        y(140),
        `${contact}`,
        '11px Lato',
        '#000',
        1,
      ),
    );
  return img;
}

export default watermarkGen;
