export const CONTENT_TYPE_MAP = {
  mp4: 'video/mp4',
  png: 'image/png',
  jpg: 'image/jpg',
  jpeg: 'image/jpeg',
};

export const getContentType = (file) => {
  const fileParts = file.name.split('.');
  const fileType = fileParts[1];
  return CONTENT_TYPE_MAP[fileType.toLowerCase()];
};

export const STALE_TIME = 1000 * 60 * 60;

