import React, { useState, useEffect } from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import { getPackages } from 'modules/packages/services/queries';
import { usePaginatedQuery } from 'react-query';
import { GET_PACKAGES } from 'modules/shared/utils/consts';
import Loading from 'modules/shared/components/Loading';
import {
  Container,
  Button,
  Select,
  CardContent,
  MenuItem,
  Typography,
  FormControl,
  InputLabel,
} from '@material-ui/core';
import Input from 'modules/shared/components/Formik/Input';
import RadioGroup from 'modules/shared/components/Formik/RadioGroup';
import useNotification from 'modules/shared/hooks/useNotification';

import selo from './unnamed.png';
import watermark from '../../shared/utils/watermark';
import createWatermark from '../services/update';

const validationSchema = Yup.object({
  name: Yup.string().required('O campo  obrigatrio.'),
  email: Yup.string()
    .email('O e-mail deve ser vlido.')
    .required('O campo  obrigatrio.'),
  role: Yup.string().required('O campo  obrigatrio.'),
  instagram: Yup.string().required('O campo  obrigatrio.'),
  phone: Yup.string().required('O campo  obrigatrio.'),
});

const PostPurchaseForm = () => {
  const [processed, setProcessed] = useState(false);
  const notification = useNotification();
  const [img, setImage] = useState(selo);
  const roleOptions = [
    { value: '0', label: 'Consultora' },
    { value: '1', label: 'Diretora' },
  ];

  const contactOptions = [
    { value: '0', label: 'Instagram' },
    { value: '1', label: 'Celular' },
  ];
  const [formValues, setFormValues] = useState({
    name: '',
    lastName: '',
    role: '0',
    contact: '0',
    instagram: '',
    phone: '',
    packageId: '',
  });
  const { isLoading, resolvedData: result } = usePaginatedQuery(
    [GET_PACKAGES, 1],
    getPackages,
  );
  useEffect(() => {
    async function fetchWatermark() {
      const generatedWatermark = await watermark(selo, formValues);
      setImage(generatedWatermark);
    }
    fetchWatermark();
  }, [formValues]);

  const onSubmit = async () => {
    try {
      await createWatermark(formValues, img);
      setProcessed(true);
      // navigate to confirm (should receive a token confirm)
    } catch (_error) {
      // print the correct error!
      notification.error('Erro ao processar');
    }
  };
  if (isLoading) {
    return <Loading />;
  }

  const packages = result.data.data.map(({ id, name }) => ({
    value: id,
    label: name,
  }));
  return (
    <Container className="d-flex justify-content-center min--100 align-items-center">
      <CardContent className="p-4">
        {processed ? (
          <>
            <Typography variant="h5" className="mb-4 text-center">
              Ps Compra
            </Typography>
            <Typography variant="body1">
              Seus dados foram processados.
            </Typography>
            <Typography
              variant="body2"
              className="text-center mt-2 font-weight-bold"
            >
              Voc pode fechar esta pgina :)
            </Typography>
          </>
        ) : (
          <Formik
            initialValues={{
              name: '',
              lastName: '',
              email: '',
              role: '0',
              instagram: '',
              contact: '0',
              phone: ' ',
            }}
            validationSchema={validationSchema}
            onSubmit={onSubmit}
          >
            {(props) => {
              const { handleChange } = props;
              if (props.values.role !== formValues.role) {
                setFormValues({
                  ...formValues,

                  role: props.values.role,
                });
              }
              if (props.values.contact !== formValues.contact) {
                setFormValues({
                  ...formValues,

                  contact: props.values.contact,
                });
              }
              const onChange = (e) => {
                const targetEl = e.target;
                const fieldName = targetEl.name;
                setFormValues({
                  ...formValues,

                  [fieldName]: targetEl.value,
                });
                return handleChange(e);
              };
              return (
                <Form>
                  <Typography variant="h5" className="mb-4 text-center">
                    Crie um novo cliente
                  </Typography>
                  <Field
                    name="name"
                    label="Nome"
                    component={Input}
                    onChange={onChange}
                  />
                  <Field
                    name="lastName"
                    label="Sobrenome"
                    component={Input}
                    onChange={onChange}
                  />
                  <Field
                    name="email"
                    label="E-mail"
                    className="mt-2"
                    component={Input}
                    onChange={onChange}
                  />
                  <Field name="package" label="Pacote">
                    {({ field: { value } }) => (
                      <FormControl variant="outlined" className="w-100">
                        <InputLabel id="package-select-label">
                          Pacote
                        </InputLabel>
                        <Select
                          labelId="package-select-label"
                          id="package-select"
                          onChange={onChange}
                          value={value}
                          name="packageId"
                        >
                          {packages.map(({ value: pckgValue, label }) => (
                            <MenuItem id={pckgValue} value={pckgValue}>
                              {label}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    )}
                  </Field>
                  <Field
                    name="role"
                    label="Cargo"
                    component={RadioGroup}
                    onChange={onChange}
                    options={roleOptions}
                  />
                  <Field
                    name="instagram"
                    label="Instagram"
                    className="mt-2"
                    onChange={onChange}
                    component={Input}
                  />
                  {/* use mask here */}
                  <Field
                    name="phone"
                    label="Celular"
                    className="mt-2"
                    onChange={onChange}
                    component={Input}
                  />
                  <Field
                    name="contact"
                    label="Contato"
                    component={RadioGroup}
                    onChange={onChange}
                    options={contactOptions}
                  />
                  <img
                    src={img.src}
                    style={{
                      width: '100%',
                      minHeight: '100%',
                      borderRadius: '2px',
                    }}
                  />
                  <div className="d-flex justify-content-end mt-4">
                    <Button
                      className="ml-4 "
                      type="submit"
                      variant="contained"
                      color="primary"
                    >
                      Salvar
                    </Button>
                  </div>
                </Form>
              );
            }}
          </Formik>
        )}
      </CardContent>
    </Container>
  );
};

export default PostPurchaseForm;
