/* eslint-disable no-console */
import React from 'react';
import { Button, Container, Typography } from '@material-ui/core';

export default function ErrorInfo({ errors }) {
  const onErrorClick = () => {
    for (let i = 0; i < errors.length; i += 1) {
      console.log(errors[i]);
    }
  };
  return (
    <Container className="d-flex flex-column justify-content-center align-items-center h-500">
      <Typography variant="h4">Ocorreu um erro!</Typography>
      <Typography variant="body1" className="mt-4">
        Entre em contato com o suporte!
      </Typography>
      <Button color="primary" onClick={onErrorClick}>
        Gerar log
      </Button>
    </Container>
  );
}
