import React from 'react';
import Container from '@material-ui/core/Container';
import { useParams } from 'react-router-dom';
import Loading from 'modules/shared/components/Loading';
import {
  GET_ALL_CATEGORIES,
  GET_PACKAGE_BY_ID,
  GET_PACKAGE_POSTS_BY_ID,
} from 'modules/shared/utils/consts';
import { useQuery } from 'react-query';
import { getCategories } from 'modules/categories/services/categories';
import ErrorInfo from 'modules/shared/components/ErrorInfo';
import PackageFormDialog from '../components/PackageFormDialog';
import { getPackageById, getPackagePostsById } from '../services/queries';
import usePackageUpdate from '../hooks/usePackageUpdate';

const EditPackage = () => {
  const { dialog, askForSave } = usePackageUpdate();
  const params = useParams();
  const postsService = () =>
    getPackagePostsById({ id: params.id, page: 1, limit: 1000 });

  const {
    isLoading: postsLoading,
    error: postsError,
    data: postsResult,
  } = useQuery(GET_PACKAGE_POSTS_BY_ID, postsService);
  const {
    isLoading: categoriesLoading,
    error: categoriesError,
    data: categoriesResult,
  } = useQuery(GET_ALL_CATEGORIES, () =>
    getCategories({ page: 1, limit: 1000 }),
  );

  const {
    isLoading: packageLoading,
    error: packageError,
    data: packageResult,
  } = useQuery(GET_PACKAGE_BY_ID, () => getPackageById({ id: params.id }));

  if (packageLoading || postsLoading || categoriesLoading) {
    return <Loading />;
  }

  if (packageError || postsError || categoriesError) {
    return <ErrorInfo errors={[packageError, postsError, categoriesError]} />;
  }

  const categories = categoriesResult.data.data.reduce(
    (acum, current) => ({
      ...acum,
      [current.id]: current,
    }),
    {},
  );

  return (
    <Container>
      <PackageFormDialog
        packageById={{
          id: packageResult.data.id,
          name: packageResult.data.name,
          description: packageResult.data.description,
        }}
        posts={postsResult.data.posts}
        categories={categories}
        submit={askForSave}
      />
      {dialog}
    </Container>
  );
};

export default EditPackage;
