import dataURItoBlob from 'modules/shared/utils/dataUrlToblob';
import API from 'config/api';
import APIs3 from 'config/apiS3';

async function createWatermark(formValues, img) {
  const email = formValues.email;
  const name = `${formValues.name && formValues.name.trim()} ${
    formValues.lastName && formValues.lastName.trim()
  }`;
  const phone = formValues.phone.trim();

  try {
    let createdUser;
    const { data: user } = await API.get(`/customers/?email=${email}`);
    if (!user) {
      createdUser = await API.post('/customers', {
        email,
        custom_name: name,
        name,
        custom_phone: phone,
        rank: formValues.role,
      });
    }
    const { data : { id, aws_logo_path, uploadURL } = {} } = await API.get('/purchases/generate-logo-url');
    await APIs3.put(uploadURL, dataURItoBlob(img.src));

    await API.post('/purchases', {
      id,
      loja_integrada_pedido_id: 1,
      aws_logo_path,
      package_id: formValues.packageId,
      customer_id: (user && user.id) || createdUser.data.id,
      custom_name: name,
      custom_phone: phone,
      rank: formValues.role,
      is_paid: true
    });
  } catch (e) {
    console.log('createWatermark');
  }
}
export default createWatermark;
