import React from 'react';
import Container from '@material-ui/core/Container';
import Loading from 'modules/shared/components/Loading';
import useGetCategories from 'modules/categories/hooks/useGetCategories';
import ErrorInfo from 'modules/shared/components/ErrorInfo';
import usePackageAdd from '../hooks/usePackageAdd';
import PackageFormDialog from '../components/PackageFormDialog';

const NewPackage = () => {
  const { dialog, askForSave } = usePackageAdd();
  const { isLoading, error, categoriesById } = useGetCategories();

  if (isLoading) {
    return <Loading />;
  }

  if (error) {
    return <ErrorInfo errors={[error]} />;
  }

  // console.log('passei', isLoading, error);
  // if (Object.keys(categories).length < 1) {
  //   // notification.error(
  //   //   'Cadastre pelo menos um tipo de categoria, antes de criar um pacote.',
  //   // );
  //   return <Redirect to="/packages" />;
  // }

  return (
    <Container>
      <PackageFormDialog categories={categoriesById} submit={askForSave} />
      {dialog}
    </Container>
  );
};

export default NewPackage;
